/*******************************
            Colors

---  Colors conventions are defined in color.variables. ---

*******************************/
/* Color one */
.color-one {
  background: #00F0F0 !important;
  color: #fff !important;
  -webkit-transition: background, color 0.1s ease;
  -moz-transition: background, color 0.1s ease;
  -ms-transition: background, color 0.1s ease;
  -o-transition: background, color 0.1s ease;
  transition: background, color 0.1s ease;
}
.color-one:hover,
.color-one:active,
.color-one:focus {
  background: #11F1F1 !important;
}
/* === Color two === */
.color-two {
  background: #0FFF00 !important;
  color: #fff !important;
  -webkit-transition: background, color 0.1s ease;
  -moz-transition: background, color 0.1s ease;
  -ms-transition: background, color 0.1s ease;
  -o-transition: background, color 0.1s ease;
  transition: background, color 0.1s ease;
}
.color-two:hover,
.color-two:active,
.color-two:focus {
  background: #1FFF11 !important;
}
/* === Color three === */
.color-three {
  background: #F000FF !important;
  color: #4C4C4C !important;
  -webkit-transition: background, color 0.1s ease;
  -moz-transition: background, color 0.1s ease;
  -ms-transition: background, color 0.1s ease;
  -o-transition: background, color 0.1s ease;
  transition: background, color 0.1s ease;
}
.color-three:hover,
.color-three:active,
.color-three:focus {
  background: #F111FF !important;
}
/* === Color four === */
.color-four {
  background: #000FFF !important;
  color: #fff !important;
  -webkit-transition: background, color 0.1s ease;
  -moz-transition: background, color 0.1s ease;
  -ms-transition: background, color 0.1s ease;
  -o-transition: background, color 0.1s ease;
  transition: background, color 0.1s ease;
}
.color-four:hover,
.color-four:active,
.color-four:focus {
  background: #000EEE !important;
}
/* === Color five === */
.color-five {
  background: #F0FFF0 !important;
  color: #fff !important;
  -webkit-transition: background, color 0.1s ease;
  -moz-transition: background, color 0.1s ease;
  -ms-transition: background, color 0.1s ease;
  -o-transition: background, color 0.1s ease;
  transition: background, color 0.1s ease;
}
.color-five:hover,
.color-five:active,
.color-five:focus {
  background: #F1FFF1 !important;
}
/* === Other color style. === */
.dark {
  color: #808080 !important;
}
.darker {
  color: #333 !important;
}
.white {
  color: #fff !important;
}
