/* Style for list layout (wishlist page)  */
/*----------------------------------------*/
/*----------------------------------------*/
/*----------------------------------------*/
.list-layout-element {
  border-bottom: 1px solid #c6c6c6;
  margin-bottom: 20px;
  padding: 20px 0px;
}
/*******************************
            Site
*******************************/
/* === Transitions === */
/* Elements */
/*******************************
            Borders
*******************************/
.no-border {
  border: none !important;
}
.border {
  border: 1px solid #333;
}
.no-border-radius {
  border-radius: 0 !important;
}
/*******************************
         Breadcrumbs
*******************************/
.breadcrumb {
  padding: 24px 0;
}
.ui.breadcrumb a {
  color: #F1FFF1;
}
.ui.breadcrumb a:hover,
.ui.breadcrumb a:focus,
.ui.breadcrumb a:active {
  color: #F0FFF0;
}
/*******************************
   Alternate style of inputs
*******************************/
.btn-link {
  color: #0FFF00;
  font-weight: normal;
  border-radius: 0;
  padding: 2px 12px;
}
.btn-link,
.btn-link:active,
.btn-link.active,
.btn-link[disabled],
fieldset[disabled] .btn-link {
  background-color: transparent;
  box-shadow: none;
}
.btn-link,
.btn-link:hover,
.btn-link:focus,
.btn-link:active {
  border-color: transparent;
}
.btn-link:hover,
.btn-link:focus {
  color: #1FFF11;
  text-decoration: underline;
  background-color: transparent;
}
.btn-link[disabled]:hover,
fieldset[disabled] .btn-link:hover,
.btn-link[disabled]:focus,
fieldset[disabled] .btn-link:focus {
  color: #808080;
  text-decoration: none;
}
/*******************************
          Buy Button
*******************************/
.buybutton {
  outline: none;
}
/*******************************
            Input
*******************************/
/* === Quantity selector  === */
.qty-selector {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 28px;
  height: 28px;
  outline: none;
  margin: 0;
  padding: 0;
  background-color: transparent;
}
.qty-selector-input {
  width: 36px;
  height: 28px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
/*******************************
          Pagination
*******************************/
/* === Variables === */
/* === Pagination ===*/
.pagination {
  display: inline-block;
  padding-left: 0;
}
.pagination > li {
  display: inline;
}
.pagination > li > a,
.pagination > li > span {
  float: left;
  padding: 6px 12px;
  line-height: 1.5rem;
  text-decoration: none;
  color: #F000FF;
  background-color: #000EEE;
  border: 1px solid #000FFF;
  margin-left: -1px;
}
/* Last element of paginator */
.pagination > li:last-child > a,
.pagination > li:last-child > span {
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
}
.pagination > li > a:hover,
.pagination > li > span:hover,
.pagination > li > a:focus,
.pagination > li > span:focus {
  color: #E000EE;
  background-color: #111FFF;
  border-color: #000FFF;
}
.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
  z-index: 2;
  color: #E000EE;
  background-color: #000FFF;
  border-color: #111FFF;
  cursor: default;
}
.pagination > .disabled > span,
.pagination > .disabled > span:hover,
.pagination > .disabled > span:focus,
.pagination > .disabled > a,
.pagination > .disabled > a:hover,
.pagination > .disabled > a:focus {
  color: #777777;
  background-color: #ffffff;
  border-color: #dddddd;
  cursor: not-allowed;
}
/*******************************
         Screen Reader
*******************************/
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
/*******************************
            Text
*******************************/
.text-strikethrough {
  text-decoration: line-through;
}
.text-center {
  text-align: center !important;
}
.text-justify {
  text-align: justify;
}
.text-nowrap {
  white-space: nowrap;
}
.text-lowercase {
  text-transform: lowercase;
}
.text-uppercase {
  text-transform: uppercase;
}
.text-capitalize {
  text-transform: capitalize;
}
.text-danger {
  color: #a94442;
}
a.text-danger:hover {
  color: #843534;
}
.light {
  font-weight: 200 !important;
}
.strong {
  font-weight: bold !important;
}
/* Modules */
/*******************************
         Cart Slider
*******************************/
/* Declare variables. */
/* Cart header. */
.cart-header {
  -webkit-font-smoothing: antialiased;
  box-sizing: border-box;
  cursor: default;
  background-color: #333;
  width: 100%;
  height: 70px;
}
/* Delete button (trash icon) */
.close-button {
  cursor: pointer;
}
/* Height of each item in cart drawer */
.cart-content {
  margin-top: 2.958rem;
}
/* Used to display the agreement at the bottom of checkout. */
.cart-content-agreement p {
  opacity: 0.8;
  font-size: 0.75em;
  cursor: default;
  -webkit-transition: 0.25s ease-in-out;
  -moz-transition: 0.25s ease-in-out;
  -ms-transition: 0.25s ease-in-out;
  -o-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}
.cart-content-agreement p:hover {
  opacity: 1.0;
}
/*******************************
         Categories
*******************************/
/**
*  --- Refine Search Categories ---
**/
.absolute-scrollable {
  padding: 0 5px;
  overflow: auto;
}
#refine-search-container {
  border-right: 1px solid #EBEBEB;
}
.refine-search-component {
  height: 500px;
  overflow: auto;
  margin: 1rem 0 2rem 0;
}
.refine-search-price {
  height: 175px;
}
.refine-search-input {
  margin-bottom: 5%;
  width: 75%;
}
.tags-list > .item {
  margin-left: 1rem !important;
}
