/*-----------------------------------*/
/* Style for Category's page */
/*-----------------------------------*/
/*-----------------------------------*/
/*-----------------------------------*/
.category-logo {
  height: 120px;
  border-radius: 100px;
}
.category-header {
  background-size: cover;
  margin-top: 0px;
}
.category-title,
.category-subtitle {
  color: #fff;
}
.category-overlay {
  background-color: rgba(0, 0, 0, 0.2);
  padding: 50px;
}
/*******************************
           Dimmers
*******************************/
/* Success dimmer */
.congratulate-dimmer {
  overflow: auto;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}
/* Cancelled Order dimmer */
#cancelledOrder {
  padding: 30px 15px;
  margin-bottom: 30px;
}
/*******************************
            Footer
*******************************/
footer {
  padding: 30px;
  margin-top: 1rem;
}
footer a {
  text-decoration: none;
  color: #fff;
  -webkit-transition: color 0.1s ease-in;
  -moz-transition: color 0.1s ease-in;
  -ms-transition: color 0.1s ease-in;
  -o-transition: color 0.1s ease-in;
  transition: color 0.1s ease-in;
}
footer a:hover,
footer a:focus,
footer a:active {
  color: #E0EEE0;
}
footer ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
#footer:hover,
#footer:active {
  background-color: #000FFF !important;
}
/*******************************
           Header
*******************************/
@media only screen and (max-width: 767px) {
  .ui.stackable.menu .item {
    margin-bottom: 5px;
  }
}
.supertop {
  background: linear-gradient(to bottom, #ffffff 73%, #efefef 100%);
}
.wishlist-button {
  color: #333;
}
.wishlist-button:hover {
  text-decoration: none;
  text-color: #222;
}
.ui.horizontal.list:not(.celled) > .item:first-child {
  padding-left: 1rem !important;
}
.description a {
  color: #0FFF00;
}
